//
function getRules(){
  //
  var validateName = (rule, value, callback) => {
      let regExp =/[^\u4E00-\u9FA5]/g;
      if(regExp.test(value)){
        callback(new Error(rule.message));
      }else{
        callback();
      }
  };
  //
  var validateAZ=(rule, value, callback)=>{
    let regExp =/^[a-zA-Z]+$/;
    if(!regExp.test(value)){
      callback(new Error(rule.message));
    }else{
      callback();
    }
  };
  //
  var validateTel=(rule, value, callback)=>{

    let regExp =/^1[3456789]\d{9}$/;
    if(!regExp.test(value)){
      callback(new Error(rule.message));
    }else{
      callback();
    }
  };
  //
  var validatePhone=(rule, value, callback)=>{
    var phone = /^0\d{2,3}-?\d{7,8}$/,phone1=/^4\d{2,3}-?\d{7,8}$/,phone2=/^8\d{2,3}-?\d{7,8}$/;
    var offphone=phone.test(value)|| phone1.test(value)|| phone2.test(value);
    if(value!==''&&!offphone){
      callback(new Error(rule.message));
    }else{
      callback();
    }
  };
  //
  var validatePhoneTel=(rule, value, callback)=>{

    var mobile = /^1[3456789]\d{9}$/,phone = /^0\d{2,3}-?\d{7,8}$/,phone1=/^4\d{2,3}-?\d{7,8}$/,phone2=/^8\d{2,3}-?\d{7,8}$/;
    var offphone=mobile.test(value)|| phone.test(value)|| phone1.test(value)|| phone2.test(value);
    if(value!==''&&!offphone){
      callback(new Error(rule.message));
    }else{
      callback();
    }
  };
  //
  var validateNumber=(rule, value, callback)=>{
    let regExp =/[^\d]/g;

    if(regExp.test(value)){
      callback(new Error(rule.message));
    }else{
      callback();
    }
  };

  // var mobile = /^1[3|5|8]\d{9}$/,phone = /^0\d{2,3}-?\d{7,8}$/,phone1=/^4\d{2,3}-?\d{7,8}$/,phone2=/^800[0-9]{7}/;
  // var offphone=mobile.test(data.field.tel)|| phone.test(data.field.tel)|| phone1.test(data.field.tel)|| phone2.test(data.field.tel);
  // if(!offphone){
  //   layer.msg('');
  //   return false;
  // }



  return {
    length:{ min:0, max:100, message: '26', trigger: 'blur' },//
    required:{ required: true, message: '', trigger: 'blur' },    //
    chinese:{ validator: validateName, trigger: 'blur',message: ''}, //
    strAZaz:{ validator: validateAZ, trigger: 'blur',message: ''}, //|
    tel:{ validator: validateTel, trigger: 'blur' ,message: ''}, //
    phone:{ validator: validatePhone, trigger: 'blur' ,message: ''}, //
    phoneTel:{ validator: validatePhoneTel, trigger: 'blur' ,message: ''}, //
    number:{ validator: validateNumber, trigger: 'blur' ,message: ''}, //
    custom:{validator:null, trigger: 'blur' ,message: ''} //
  };
}

function setData(validate){
  let o={};//
  validate.map((item,index)=>{
      let r=getRules();//
      o[item.field]=[];
      if(item.type){
        for(let key in item.type){
          let d=r[key];
          d.message=item.type[key];
          o[item.field].push(d);
        }
      }

      //
      let validator=item.validator;
      if(typeof validator=="function"){
        r.custom.validator=validator;
        if(item.trigger==="change"){
          r.custom.trigger="change";
        }
        o[item.field].push(r.custom);
      }

      //
     let textCount=item.textCount;
     let lengthText=item.lengthText;
     if(textCount){
        if(textCount.length==1){
            r.length.min=textCount[0];
        }
        if(textCount.length==2){
          r.length.min=textCount[0];
          r.length.max=textCount[1];
        }

      if(lengthText){
        r.length.message=lengthText;
      }else{
        if(r.length.min){
          lengthText=""+r.length.min+""
        }
        if(r.length.max){
          if(lengthText){
            lengthText+=","+r.length.max+"";
          }else{
            lengthText=""+r.length.max+"";
          }
        }
        r.length.message=lengthText;
      }
       o[item.field].push(r.length);
    }
    //console.log(o[item.field])
  })
  return o;
}
export function verification(validate){
   return setData(validate)
};



