<template>
  <div>
    <div>
      <div class="search-box">
        
      </div>
      <el-button type="primary" size="small" round  @click="operation('add')"><i class="el-icon-plus el-icon--right"></i></el-button>
    </div>
    <!--  -->
    <div class="main">
      <!--  -->
      <TbaleList :tableData="tableData" :unfold="true" @tabBtnOperation="eventOperation" :column="column"/>
      <!--  -->
      <PageBott @currChange="pageChang" :pageMeter="pageMeter" />
    </div>
  </div>
</template>

<script>
  import PageBott from '@/components/pageBott';//
  import TbaleList from '@/components/table';//

  export default {
    components:{
     PageBott,TbaleList
    },
    data() {
      return {
        // 
        visible:false,//AddArticle
        parameter:{  //
          title:"",
          type:""
        },
        //
        pageMeter:{
          limit:10,
          page:1,
          total:0
        },
        //
        tableData:[],//
        column:[
          //
          {prop:"id",name:"ID",minWidth:100,isShowType:"default"},
          {prop:"imgurls",name:"",minWidth:170,isShowType:"img"},
          {prop:"title",name:"",minWidth:170,isShowType:"default",isUnfold:true},
          {prop:"creater",name:"",minWidth:170,isShowType:"default"},
          {prop:"create_time",name:"",minWidth:170,isShowType:"default"},
          {prop:'keyword',name:"",isShow:true,isUnfold:true},
          {prop:'describe',name:"",isShow:true,isUnfold:true},
          {prop:'issuer',name:"",isShow:true,isUnfold:true},
          {prop:'issuer_time',name:"",isShow:true,isUnfold:true},
          {prop:'update_time',name:"",isShow:true,isUnfold:true},
          {prop:'state_text',name:"",isUnfold:true},
          {prop:'state',name:"",minWidth:200,align:'center',fixed:"right",isShowType:"btn",btnArr:[
             {size:"mini",type:"primary",plain:true,name:"",field:'edit'},
             {size:"mini",type:"success",plain:true,name:"",field:'issuer',state:'ok'},
             {size:"mini",type:"warning",plain:true,name:"",field:'issuers',state:'on'},
             {size:"mini",type:"danger",plain:true,name:"",field:'del'}
          ]}
        ]
      }
    },
    created(){
      //
      this.getData(this.searchData());
    },
    methods: {
      //
      visibleClose(d){
       if(d==='ok'){
          this.getData(this.searchData());
       }
       this.visible=false;
      },
      eventOperation({type,data}){
        //
        this.operation(type,data);
      },
      operation(type,data){
        //
        this.parameter.type=type;
        if(type==='add'){
          this.parameter.title="";
          this.visible=true;
        }else if(type==='edit'){
          this.parameter.title="";
          this.visible=true;
          this.$refs.addArticle.setEditData(data.row);
        }else if(type==='del'){
          this.$refs.addArticle.setUploadData({
            id:data.row.id,
            type:'del'
          });
        }else if(type==='issuer'||type==="issuers"){
          this.$refs.addArticle.setUploadData({
            id:data.row.id,
            type:'issuer'
          });
        }
      },
      //
      onSubmit() {
        console.log('submit!');
      },
      getData(d){
        //
        const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.$request({
        	type:"get",
        	url:"index.php/apiAll/article",
          data:d,
        	success:(res)=>{
            loading.close();
            if(res.code==200){
               this.tableData=res.data.data;
               this.pageMeter.total=res.data.count;
            }else{
              this.$message({
                showClose: true,
                message: '',
                type: 'error'
              });
            }

        	},error:(err)=>{
        		this.$message.error(err);
        	}
        });
      },
      pageChang({limit,page}){
        //
        this.pageMeter.limit=limit;
        this.pageMeter.page=page;
        this.getData(this.searchData());
      },
      searchData(){
        //
        return {
          limit:this.pageMeter.limit,
          page:this.pageMeter.page
        }
      }

    }
  }
</script>

<style scoped>
@import url("../../../assets/css/main.css");
</style>



