import Vue from 'vue'
import App from './App'
import router from './router'
//
import store from './store';

Vue.config.productionTip = false

//
import config from './qdvdl/config';
Vue.prototype.$http=config.base_url;
//
Vue.prototype.$handleVerify=config.handleVerify;
//
import Api from './qdvdl/api';
Vue.prototype.$api=Api;


// Vue.prototype.$http="http://vue.admin.com:8888/";
//
import {request} from './qdvdl/axios-qdvdl';//
Vue.prototype.$request = request;

/* eslint-disable no-new */
//
import {verification} from './qdvdl/verification';//
Vue.prototype.$verification = verification;

//()
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '../static/reset.css';//
Vue.use(ElementUI);

//console.log(this.$store.getters.getData);

//
router.beforeEach((to, from, next) => {
  if(to.meta.title) {
    document.title = to.meta.title;
  }
  let login=to.meta.type;
  if(login){
    if(store.getters.getData.login){
      next()
    }else{
      next('/login')
    }
  }else{
    if(store.getters.getData.login){
        next("/");
    }else{
      next();
    }
  }
})


new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



