<template>
  <div>
    <div class="item">
      <h2 class="title">1.</h2>
        <el-upload
          class="upload-demo"
          action=""
           :accept="accept"
          :http-request="_import"
          list-type="picture">
          <el-button size="small" type="primary"></el-button>
        </el-upload>
        <ul>
          <li v-for="(item,index) in data" :key="index">{{item}}</li>
        </ul>
    </div>
    <div class="item">
      <h2 class="title">2.</h2>
      <el-button size="small" type="danger" @click="_export"></el-button>
    </div>
    <div class="item">
      <h2 class="title">3.</h2>
      <el-upload
        class="upload-demo"
        action=""
         :accept="accept"
        :http-request="uploadRequest"
        list-type="picture">
        <el-button size="small" type="danger"></el-button>
        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
      </el-upload>

    </div>
  </div>
</template>

<script>
  let XLSX = require('xlsx');
  let SparkMD5 = require('spark-md5');
  export default {
    data(){
      return{
        accept:".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel",
        data:[],
        list:[
          ["","","",""]
        ]
      }
    },
    created(){
      for(var i=0;i<10000;i++){
        this.list.push([
          ""+(i+1),
          (i+1),
          "18200213228"+i,
          "6226251991081"+i
        ])
      }
    },
    methods:{
      upFile(data){
        let axiosPromiseArray=[];
        
          //console.log(data);
          //  let param = new FormData();
          //  param.append("name", "wiiiiiinney");
          // //appendform
          //  param.append("file", file);
          // //FormDataget
          //  console.log(param.get("file"));
          
          
          this.$request({
          	type:"file",
          	url:"main/uploads",
            data:data,
          	success:(res)=>{
              console.log(res);
          	},error:(err)=>{
          		console.log(err);
          	}
          });
      },
      uploadRequest(d){
        let file=d.file;
        var blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;//
        let bytesPerPiece=1024 * 1024;//1MB .1048576=1MB
        let chunks = Math.ceil(file.size /bytesPerPiece); //
        this.fielSlice(file,(hexHash,chunks)=>{
         // console.log(res,chunks);
          for(var i=0;i<chunks;i++){
            //console.log(chunks);
              const start = i * bytesPerPiece;
              const end = Math.min(file.size, start + bytesPerPiece);
              let form=new FormData();
              //
              form.append('file',blobSlice.call(file, start, end));
              form.append('name', file.name);
              form.append('index', i);
              form.append('size', file.size);
              form.append('hash', hexHash);
              this.upFile(form);

          }

        },(err)=>{
          console.log(err);
        });

      },
      fielSlice(file,success,err){
        //
        var blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;//
        let bytesPerPiece=1024 * 1024;//1MB .1048576=1MB
        let fileSize=file.size; //
        let fileName=file.name; //
        let chunks = Math.ceil(fileSize /bytesPerPiece); //
        let currentChunk = 0; //

        let spark = new SparkMD5.ArrayBuffer(); //
        let fileReader = new FileReader();  //

        fileReader.onload=(e)=>{
          //
          //console.log(`${currentChunk}${currentChunk + 1}`,''+chunks+'');
          spark.append(e.target.result);
          currentChunk++;
          if (currentChunk < chunks) {
              var start = currentChunk * bytesPerPiece;
              let end = ((start + bytesPerPiece) >= fileSize) ? fileSize : start + bytesPerPiece;
              fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
          } else {
              const result=spark.end();
              const sparkMd5 = new SparkMD5();
              sparkMd5.append(result);
              sparkMd5.append(file.name);
              const hexHash = sparkMd5.end();
              success(hexHash,chunks)
          }

        }
        fileReader.onerror = function () {
          //
           console.warn('oops, something went wrong.');
           err('oops, something went wrong.')
        };

        var start = currentChunk * bytesPerPiece;
        let end = ((start + bytesPerPiece) >= fileSize) ? fileSize : start + bytesPerPiece;
        fileReader.readAsArrayBuffer(blobSlice.call(file, start, end));

      },
      _export(){
          const ws = XLSX.utils.aoa_to_sheet(this.list);
          const wb = XLSX.utils.book_new();
          XLSX.utils.book_append_sheet(wb, ws, "SheetJS");
          /*  */
          XLSX.writeFile(wb, "sheetjs.xlsx");
      },
      _import(d){
        const reader = new FileReader();
        reader.readAsBinaryString(d.file);
        reader.onload = (e) => {
          // 
          const bstr=e.currentTarget.result
          const wb = XLSX.read(bstr, {type:'binary'});
          //
          const wsname = wb.SheetNames[0];
          const ws = wb.Sheets[wsname];
          //json
          const data = XLSX.utils.sheet_to_json(ws, {header:1});
         // console.log(data);
          this.data=data;
        }
      },

      blobSlice(blob, startByte, endByte) {
        if (blob.slice) {
          return blob.slice(startByte, endByte);
        }
        // firefox
        if (blob.mozSlice) {
          return blob.mozSlice(startByte, endByte);
        }
        // webkit
        if (blob.webkitSlice) {
          return blob.webkitSlice(startByte, endByte);
        }
        return null;
      }

    }
  }
</script>

<style>
  .item{
    padding: 15px 0;
  }
</style>



