import Vue from 'vue'
import Router from 'vue-router'
//
import Home from '@/page/index';
//
import Login from '@/page/login';

// 
import Admin from '@/page/admin/operation/admin';//
import Role from '@/page/admin/operation/role';//
import SetAdmin from '@/page/admin/operation/setAdmin';//
import Auth from '@/page/admin/operation/auth';//
//
import Article from '@/page/admin/article';//


//
import DemoForm from '@/page/component/demo/dform';//form
import DemoUpload from '@/page/component/demo/dupload';//
import DemoTable from '@/page/component/demo/dtable';//
import Dpage from '@/page/component/demo/dpage';//
import DdimExport from '@/page/component/demo/dimExport';//


Vue.use(Router)


export default new Router({
  // mode: "history",
  // base:"/dist",
  routes: [
    {path: '/',name: 'home',component: Home,meta: {title: "",type: "login", keepAlive:false}}, //keepAlive
    // 
    {path: '/admin',name: 'admin',component: Admin,meta: {title: "-",type: "login", keepAlive:false}},
    {path: '/role',name: 'role',component: Role,meta: {title: "-",type: "login", keepAlive:false}},
    {path: '/setAdmin',name: 'setAdmin',component: SetAdmin,meta: {title: "-",type: "login", keepAlive:false}},
    {path: '/auth',name: 'auth',component: Auth,meta: {title: "-",type: "login", keepAlive:false}},
    //
    {path: '/login',name: 'login',component: Login,meta: {title: "-",type:"", keepAlive:false}},
    //
    {path: '/article',name: 'article',component: Article,meta: {title: "-",type:"login", keepAlive:false}},
    // demo
    {path: '/demoForm',name: 'demoForm',component: DemoForm,meta: {title: "-form",type:"login",keepAlive:false}},
    {path: '/demoUpload',name: 'demoUpload',component: DemoUpload,meta: {title: "-",type:"login",keepAlive:false}},
    {path: '/demoTable',name: 'demoTable',component: DemoTable,meta: {title: "-",type:"login",keepAlive:false}},
    {path: '/dpage',name: 'dpage',component: Dpage,meta: {title: "-",type:"login",keepAlive:false}},
    {path: '/dimExport',name: 'dimExport',component: DdimExport,meta: {title: "-",type:"login",keepAlive:false}},
  ]
})



