<template>
  <div class="form-box">
    <CreateForm ref="myForm" labelWidth="90px" :rules="rules" @eventSubmit="onSubmit" refNanme="sbmit" bntText="" :formData="formData" :formListItem="formListItem()"/>
  </div>
</template>

<script>
  import CreateForm from "@/components/form";
  export default {
    components:{CreateForm},
    name:"addForm",
    data(){
      return{
          formData:this.setFormData(),
          rules:this.verification()
      }
    },
    methods:{
        showFormData({data,type}){
           //
           if(type==="edit"){
             let {id,title,imgurl,keyword,describe,content,state}=data.row;
             let d={
               title:title,
               keyword:keyword,
               describe:describe,
               content:content,
               state:state==='on' ? false : true,
               img:imgurl,
               imageUrl:[]
             }
             if(imgurl){
                d.imageUrl=imgurl.split(",");
             }
             this.formData=d;
             this.$refs.myForm.getEditor(d.content,0);//
             //this.$refs.myForm.getEditor("sadfsadf111111sadf",1);//
           }else{
             this.formData=this.setFormData();
             this.$refs.myForm.clearEditor(0);//
             //this.$refs.myForm.clearEditor(1);//
           }
        },
        onSubmit({data}){
          //
          this.$emit('evnetPost',data);
        },
        setFormData(){
          //
          return {
            title:"",
            keyword:"",
            describe:"",
            content:"",
            state:false,
            img:"",
            imageUrl:[]
          }
        },
        verification(){
          //
          var vimg = (rule, value, callback) => {
           // console.log(value)
            if (value!==""&&value.split(",").length<1) {
              callback(new Error('1!'));
            } else {
              callback();
            }
          };
           //
          var vcontent=(rule, value, callback)=>{
            //console.log(value.replace(/<[^>]*>/g))
            if (value!==""&&value.replace(/<[^>]*>/g,'')==="") {
              callback(new Error('!'));
            } else {
              callback();
            }
          };
          //
          var v=[
             {field:"title",type:{required:""},textCount:[2,25]},
             {field:"img",validator:vimg,type:{required:""}}, //
             {field:"content",validator:vcontent,type:{required:""}}
          ];
          v=this.$verification(v);
          return v;
        },
        formListItem(){
          //
          let form=[
            {prop:"title", maxlength:25,placeholder:"",label:'',type:'text'},
            {prop:"keyword", maxlength:255,placeholder:"()",label:'',type:'text'},
            {prop:"describe", maxlength:255,placeholder:"",label:'',type:'textarea',height:"auto"},
            {prop:"img", label:'1',type:'imgUpload',placeholder:'288*188;',
               uploadParame:{
                  data:{path:"active"},//
                  limit:1,//
                  action:"index.php/main/upload_file", //
               },
               name:'imageUrl' //formDatakeyformData[key]
            },
            {prop:"state",label:'',type:'switch'},//
            {prop:"content",label:'',type:'editConten'}
          ];
          return form;
        }
    }
  }
</script>

<style scoped>
  .item{
    background: green;
    margin-bottom: 20px;
    height: 180px;
  }
  .form-box{
    padding-top: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
  }
</style>



