<template>
  <div>
    <div ref="editor"></div>
  </div>
</template>

<script>
  import E from 'wangeditor';
  export default {
    name:'editor',
    data(){
     return{
        editorObj:null
     }
    },
    props:{
      name:String
    },
    methods:{
      getContent(html){
        let data={
          name:this.name,
          content:html, //
        }
        this.$emit('getContent',data)
      },
      getEditor(e){
        this.editorObj=e;
        let data={
          name:this.name,
          e:e
        };
        this.$emit('getEditors',data);
      },
      editor(){
        return {
          name:this.name,
          editor:this.editorObj
        };
      }
    },
    mounted(){
      let e= new E(this.$refs.editor);


      //  e.txt.html(content);
      // e.txt.clear();//
      //
      e.customConfig.menus = [
        'head',  // 
        'bold',  // 
        'fontSize',  // 
        'fontName',  // 
        'italic',  // 
        'underline',  // 
        'strikeThrough',  // 
        'foreColor',  // 
        'backColor',  // 
        'link',  // 
        'list',  // 
        'justify',  // 
        // 'quote',  // 
        //'emoticon',  // 
        'image',  // 
        'table',  // 
        //'video',  // 
        //'code',  // 
        'undo',  // 
        'redo'  // 
      ]
      //
      e.customConfig.colors =[
        "#000000","#ffffff","#c0c0c0","#808080","#ffcc00","#00ff00","#808000","#008000","#008080","#00ffff","#0000ff"
        ,"#000080","#ff00ff","#ff0000","#800080","#880000"
      ];

      e.customConfig.pasteFilterStyle = false; //
      e.customConfig.pasteIgnoreImg = false;   //

      e.customConfig.showLinkImg = true;// 
      e.customConfig.uploadImgShowBase64 = true  // base64 

      e.customConfig.uploadImgMaxSize = 3 * 1024 * 1024;//5m
      e.customConfig.uploadImgMaxLength = 5;//5

      e.customConfig.customAlert = (info)=>{ //
          // info 
          this.$message.error(info);
      }
      e.customConfig.onchange = (html) => {
        // this.content = html
        this.getContent(html);
      }
      e.create()
      this.getEditor(e);
    }
  }
</script>





