<template>
 <div>
      <!--  -->
       <el-drawer
         :visible.sync="visible"
         direction="rtl"
         size="50%"
         :show-close="false"
         :wrapperClosable='false'
         :before-close="dialogClose">
         <div slot="title" class="title">{{title}}<div class="icon" @click="dialogClose"><i class="el-icon-close"></i></div></div>
         <div class="drawer-box">
           <!--  -->
              <el-form inline="inline" label-position="right" status-icon ref="postData" label-width="80px" :rules="postRules" :model="postData" class="demo-ruleForm">
                <el-form-item label="" prop="role_name">
                 <el-input v-model="postData.role_name" maxlength="6" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="remark">
                 <el-input v-model="postData.remark" maxlength="20"  placeholder="" ></el-input>
                </el-form-item>
              </el-form>
              <div class="authbox-list">
                <!--   -->
                <el-checkbox-group v-model="checkList" @change="handleCheckedCitiesChange">
                  <div v-for="item in auth" :key="item.id">
                    <el-divider><span>{{item.title}}</span></el-divider>
                    <div class="auth-list" v-for="items in item.children" :key="items.id">
                        <el-checkbox :label="items.id" >{{items.title}}</el-checkbox>
                        <el-checkbox :label="itemss.id" v-for="itemss in items.children" :key="itemss.id">{{itemss.title}}</el-checkbox>
                        <el-divider></el-divider>
                    </div>
                  </div>
                </el-checkbox-group>
              </div>
             <div slot="footer" class="dialog-footer">
               <el-button size="small" @click="dialogClose"> </el-button>
               <el-button size="small" type="primary" @click="submitForm('postData')"> </el-button>
             </div>
         </div>
      </el-drawer>

 </div>
</template>

<script>
  export default {
    data(){
      return{
        title:"",  //
        visible:false,//
        postData:{    //
          role_name:"",
          remark:"",
          auth_id:""
        },
        type:"", //
        postRules:this.verification(), //
        checkList: []  //
      }
    },
    props:{
      auth:Array|Object
    },
    methods:{
      //
      handleCheckedCitiesChange(value){
        this.checkList=value;
        this.postData.auth_id=value.join(",");
      },
      //
      verification(){
        //
        var v=[
           {field:"role_name",type:{required:"",chinese:""},textCount:[2,6]},
           {field:"remark",textCount:[0,20]}
        ];
        v=this.$verification(v);
        return v;
      },
      //
      submitForm(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let data=this.postData;
            data.type=this.type;
            this.submitData(data);
          } else {
            return false;
          }
        });
      },
      //
      submitData(data){
          const loading = this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          this.$request({
          	type:"post",
          	url:this.$api.addRole,
            data:data,
          	success:(res)=>{
              loading.close();
              if(res.code==200){
                this.dialogClose('load');
                this.$message({
                  showClose: true,
                  message: res.msg,
                  type: 'success'
                });
              }else{
               this.$message({
                 showClose: true,
                 message: res.msg,
                 type: 'error'
               });
              }

          	},error:(err)=>{
          		this.$message.error(err);
          	}
          });
      },
     //
      dialogClose(t){
        this.postData={
          role_name:"",
          remark:"",
          auth_id:""
        }
        this.visible=false;
        this.checkList=[];
        this.$emit('close',t)
      },
      operation(type,data={row:{}}){
        //
        let {id,role_name,remark,auth_id}=data.row;
        this.type=type;
        if(type==="edit"){
          this.title="";
          this.postData={
            id:id,
            role_name:role_name,
            remark:remark,
            auth_id:auth_id
          };
          this.checkList=auth_id.split(',');
          this.visible=true;
        }else if(type==="del"){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.submitData({id:id,type:'del'});
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '!'
            });
          });
        }else if(type==="add"){
          this.title="";
          this.visible=true;
        }
      }
    }
  }

</script>

<style scoped>
@import url("../../../assets/css/main.css");
.auth-list li{
  overflow: hidden;
}
.b-title{
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  font-weight: 400;
}

.title{
  padding:0 20px;
  height:60px;
  line-height: 60px;
  font-size: 16px;
  border-bottom: 1px solid #f7f7f7;
}
.title .icon{
 float: right;
 cursor: pointer;
}
.authbox-list{
  min-width: 600px;
  max-height:75vh;
  overflow: auto;
  margin-bottom: 15px;
}
.drawer-box{
  min-width: 800px;
}
.dialog-footer{
  text-align: left;
}
</style>



