<template>
  <div class="search-box search-box-bg-null">
    <CreateForm :inline="inline" labelWidth="100px" :rules="rules" @eventSubmit="onSubmit" refNanme="sbmit" bntText="" :formData="formData" :formListItem="formListItem()"/>
  </div>
</template>

<script>
import CreateForm from "@/components/form";
export default {
  components:{
    CreateForm
  },
  data(){
    return{
        inline:false,
        formData:{
          title:'',
          name:'',
          tel:'',//
          phone:'',//
          phoneTel:'',//
          age:'',
          des:'',
          state:'',//select
          type:[],//
          sex:"",//false
          open:false,//
          datetime:"2019-09-09 00:00:00",           //
          datetimerange:["2019-09-09","2019-09-15"],//
          cascader:[], //value
          img:"",     //1
          imglist:[], //1
          content:"<p></p>",//
        },
        rules:this.verification()
    }
  },
  methods:{
    onSubmit({data}){
      //
      console.log(data);

    },
    //
    verification(){
      //
      var validatePass2 = (rule, value, callback) => {
        if (value!==""&&value.split(",").length<2) {
          callback(new Error('2!'));
        } else {
          callback();
        }
      };

      var v=[
         {field:"title",type:{required:""},textCount:[6,25]},
         {field:"name",type:{required:"",chinese:''},textCount:[2,9]},
         {field:"tel",type:{required:"",tel:'11'},textCount:[11]},
         {field:"phone",type:{phone:'xxx-xxxxxxx'},textCount:[11]},
         {field:"phoneTel",type:{required:"",phoneTel:'xxx-xxxxxxx11'},textCount:[11]},
         {field:"img",validator:validatePass2,type:{required:""}}, //
         {field:"age",type:{number:''},textCount:[1,3]},
      ];
      v=this.$verification(v);
      return v;
    },
    formListItem(){
      let item=[
         {prop:"title", maxlength:25,placeholder:"",label:'',type:'text'},//text
         {prop:"name", maxlength:3,placeholder:"",label:'',type:'text'},
         {prop:"tel", maxlength:11,placeholder:"",label:'',type:'text'},
         {prop:"phone", maxlength:12,placeholder:"",label:'',type:'text'},
         {prop:"phoneTel", maxlength:12,placeholder:"",label:'',type:'text'},
         {prop:"age", maxlength:3,placeholder:"",label:'',type:'text'},
         {prop:"des", maxlength:30,placeholder:"",label:'',type:'textarea',height:"auto"}, //textarea
         {prop:"img", label:'1',type:'imgUpload',placeholder:'3288*188;',
            uploadParame:{
               data:{path:"active"},//
               limit:3,//
               action:this.$http+"index.php/main/upload_file", //
            },
            name:'imglist' //formDatakeyformData[key]
         },//
         {prop:"state",placeholder:"",label:'',type:'select',select:[
           {text:"",value:"ok"},
           {text:"",value:"on"}
         ]},//select
         {prop:"type",label:'',minlength:0,maxlength:2,type:'text',type:'checkbox',checkbox:[
           {text:"",value:"1"},
           {text:"",value:"2"},
           {text:"",value:"3"},
           {text:"",value:"4"}
         ]},//
         {prop:"sex",label:'',type:'radio',radio:[
           {text:"",value:"1"},
           {text:"",value:"2"},
           {text:"",value:"3"},
           {text:"",value:"4"}
         ]},//
         {prop:"open",label:'',type:'switch'},//
         //type--  datetime--date
         {prop:"datetime",label:'',type:'datetime',placeholder:''},
         // type--  datetimerange--daterange
         {prop:"datetimerange",label:'',type:'daterange',range:'',startPlaceholder:'',endPlaceholder:''},
         //options
         {prop:"cascader",label:'',type:'cascader',placeholder:'',options:[
           {
             key:"1",
             value:"qdkf",
             label:"",
             children:[
                {value: 'css',label: ''},
                {value: 'js',label: 'javascript',
                  children:[
                    {value:'vue',label:"vue"},
                    {value:'react',label:"react"}
                  ]
                },
                {value: 'html',label: 'HTML'}
             ]
           }
         ]},
         {prop:"content",label:'',type:'editConten',height:"auto"}
       ];
       return item;
    }
  }
}
</script>

<style scoped>
.search-box{
  background: #f2f2f2;
  padding:10px 10px 4px 10px;
  margin: 10px 0;
}
.search-box .el-form-item{
  margin-bottom: 6px;
}
.search-box-bg-null{
  background: #fff;
}
</style>



