<template>
  <div>
    <div class="croppers" v-bind:style="{width:'100%',height:croppParame.croppersH}">
       <VueCropper
        ref="cropper"
        :canMoveBox="canMoveBox"
        :fixedBox="fixedBox"
        :autoCrop="autoCrop"
        :autoCropWidth="croppParame.autoCropWidth"
        :autoCropHeight="croppParame.autoCropHeight"
        :mode="mode"
        :img="img"
        :infoTrue="infoTrue"
        :centerBox="centerBox"
        :canMove="canMove"
        :outputSize="1"
        @realTime="realTime"
        />
    </div>


     <!--  -->
    <div class="cropper-btn">
      <el-button title=""  @click="imgOperation('size_fd')" type="primary" icon="el-icon-zoom-in" circle></el-button>
      <el-button title="" @click="imgOperation('size_sx')"  type="success" icon="el-icon-zoom-out" circle></el-button>
      <el-button title="" @click="imgOperation('crop')"  type="info" icon="el-icon-camera" circle></el-button>
      <el-button title="" @click="imgOperation('rotateLeft')" type="warning" icon="el-icon-refresh-left" circle></el-button>
      <el-button title="" @click="imgOperation('rotateRight')" type="danger" icon="el-icon-refresh-right" circle></el-button>
    </div>



  </div>
</template>

<script>
  import { VueCropper }  from 'vue-cropper';

  export default {
    components:{VueCropper},
    name:"imgCrop",
    data(){
      return{
          // 
          mode:"auto",//
          autoCrop:true,//
         // autoCropWidth:"300", //
         // autoCropHeight:"300", //
          fixedBox:true,//
          canMoveBox:false,//
          canMove:true, //
         // img:"",// url  || base64 || blob
          centerBox:false,//<
          infoTrue:false,
          blobimg:"",//
          isShowBlob:false
      }
    },
    props:{
      img:String, // url  || base64 || blob
      croppParame:Array|Object
    },
    mounted(){
      //this.$refs.cropper.cropW='100px';
     // this.$refs.cropper.cropH="100px";
    },
    methods:{
      imgOperation(type){
        if(type==="size_sx"){
          //
          this.$refs.cropper.changeScale(-5);
        }else if(type==="size_fd"){
          //
          this.$refs.cropper.changeScale(5);
        } else if(type==="crop"){
          //
          this.$refs.cropper.startCrop()
          this.$refs.cropper.getCropData((data) => {
              this.$emit('getCropDatas',{data:data});
              // this.blobimg=data;
              // this.isShowBlob=true;
          })

        }else if(type==="rotateLeft"){
          this.$refs.cropper.rotateLeft(); //90
        }else if(type==="rotateRight"){
          this.$refs.cropper.rotateRight(); //90
        }

      },
      realTime(data){
        //console.log(data)
      }
    }
  }
</script>

<style>

.preview{
  width: 300px;
  height: 300px;
  overflow: hidden;

}
.preview img{
  max-width: 100%;
}
.cropper-btn{
  text-align: center;
  padding-top: 40px;
}
.cropper-img{
  /* background: red; */
  text-align: center;
}
.pmsg{
  margin-bottom: 15px;
  color:#b3b3b3;
  font-size: 12px;
}
</style>



