<template>
 <!--  -->
 <el-dialog :title="title" :visible="visible" width="363px" @close="dialogClose" >
   <el-form label-position="right" status-icon ref="postData" label-width="80px" :rules="postRules" :model="postData" class="demo-ruleForm">
     <div v-if="vtype==='set'">
       <el-form-item label="" prop="password" v-if="type!='edit'">
           <el-input type="password" maxlength="18" v-model="postData.password" placeholder=""  autocomplete="off"></el-input>
       </el-form-item>
       <el-form-item label="" prop="checkPass" v-if="type!='edit'">
           <el-input type="password" maxlength="18" v-model="postData.checkPass" placeholder=""  autocomplete="off"></el-input>
       </el-form-item>
     </div>
     <div v-if="vtype==='img'">
       <el-form-item label="" prop="headimg">
          <Upload  :croppParame="croppParame" :isCrop="true" :item="0" :imgList="imageUrl" :parame="uploadParame" @onSuccess="getImage"></Upload>
       </el-form-item>
     </div>
   </el-form>
   <div slot="footer" class="dialog-footer">
     <el-button size="small" @click="dialogClose"> </el-button>
     <el-button size="small" type="primary" @click="submitForm('postData')"> </el-button>
   </div>
 </el-dialog>
</template>

<script>
  import Upload from '@/components/upload';
  export default {
    name:"SetUserInfo",
    components:{Upload},
    data(){
      return{
          title:"",
          postData:{
            password:"",
            checkPass:"",
            headimg:""
          },//
          postRules:this.verification(), //
          type:"", //
          imageUrl:[], //
          uploadParame:{
            data:{path:"admin"},//
            limit:1,//
            action:this.$api.upload,//
          },
          croppParame:{
            croppersW:"400px",//
            croppersH:"300px",//
            autoCropWidth:"200", //
            autoCropHeight:"200", //
            top:"30vh",           //
            topCrop:"25px"       //
          }
      }
    },
    props:{
      visible:Boolean,
      vtype:String
    },
    created(){
      let imgsrc=this.$store.getters.getData.userInfo.headimg;
      if(imgsrc){
         this.imageUrl=imgsrc.split(",");
      }
    },
    methods:{
      //
      verification(){
        //
        var validatePass2 = (rule, value, callback) => {
          if(value===""){
            callback(new Error('!'));
          }else if(value===this.postData.password) {
            callback(new Error('!'));
          } else {
            callback();
          }
        };
        var v=[
           {field:"password",type:{required:""},textCount:[6,18]},
           {field:"checkPass",validator:validatePass2,textCount:[6,18]} //
        ];
        v=this.$verification(v);
        return v;
      },
      dialogClose(type){
        this.$emit("visibleClose",type);
      },
      getImage({set,imgUrl,index}){
        if(set.type==="add"){
          //
          this.imageUrl.push(imgUrl);
          // this.imageUrl=imgUrl;
        }else if(set.type==="edit"){
          //
          // this.imageUrl=imgUrl;
         this.$set(this.imageUrl,set.index,imgUrl);
        }else if(set.type==="del"){
          //
          // this.imageUrl=[];
          this.imageUrl=this.imageUrl.filter((item,i)=>{
            if(set.index!=i){
              return item;
            }
          });
        }
      },
      submitForm(formName){
        this.postData.headimg=this.imageUrl.join(",");
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let data=this.postData;
            data.vtype=this.vtype;
           // console.log(data)
            this.submitData(data);
          } else {
            // console.log('error submit!!');
            return false;
          }
        });
      },
      submitData(data){
        // console.log(data)
        // return ;
          const loading = this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          this.$request({
          	type:"post",
          	url:"index.php/api/setIinfo",
            data:data,
          	success:(res)=>{
              loading.close();
              if(res.code==200){
                if(this.vtype==="set"){
                   this.dialogClose('ok');
                }
                if(this.vtype==="img"){
                  this.$store.dispatch('setUserinfos',{
                    key:"headimg",
                    data:this.imageUrl.join(",")
                  });
                  this.dialogClose('no');
                }
              }else{
               this.$message({
                 showClose: true,
                 message: res.msg,
                 type: 'error'
               });
              }

          	},error:(err)=>{
          		this.$message.error(err);
          	}
          });
      }
    }
  }
</script>

<style>
  .dialog-footer{
    text-align: center;
  }

</style>



