// www.qdvdl.com

import axios from 'axios';
import qs from 'qs';


//const apiUrl="http://gnjf.sctshd.com";//API
//const apiUrl="http://vue.admin.com/index.php";//API

//
import config from './config';
const apiUrl=config.site_url; //API
const userSID=config.userSID;//

// console.log(config);
let auth={
  username:config.user_name,
  password:config.user_pass,
};

const setHeader={
	'Content-Type': 'application/x-www-form-urlencoded'
}

//
const instance=axios.create({
  auth: auth,
	baseURL:apiUrl,
	withCredentials: true, //
	responseType:"json",
	headers:setHeader,
	transformRequest: [function (data) {
    //console.log(data)
		if(data){
			data=qs.stringify(data)
		}
		return data;
	}]
})


function get(url,params,success,error){
	instance.get(url,{
		params:params
	}).then((res)=>{
		if(res.status===200){
			success(res.data)
		}else{
			error(',!')
		}
	}).catch((err)=>{
		error('!');
	})
}

function post(url,params,success,error){
	instance.post(url,params).then((res)=>{
		if(res.status===200){
			success(res.data)
		}else{
			error(',!');
		}
	}).catch((err)=>{
	//	console.log(err)
		error('!');
	})
}
let axiosPromiseArray=[];
function upload(url,params,success,error){
    let fConfig = {
      //
      baseURL:apiUrl,
      headers: { "Content-Type": "multipart/form-data" },
      //
      onUploadProgress: e => {
        var completeProgress = ((e.loaded / e.total * 100) | 0) + "%";
        console.log(completeProgress);
      }
    };

    axiosPromiseArray.push(axios.post(url,params,fConfig));

    axios.all(axiosPromiseArray).then((res)=>{
        success(res)
    }).catch((err)=>{
      error('!');
      
    })

     //    }
     //    // 
     //    await axios.all(axiosPromiseArray).then(() => {


    // axiosPromiseArray.push
    // axios.post(url, params, fConfig).then((res)=>{
    //     success(res)
    // }).catch((err)=>{
    //     error('!');
    // });
}

//
export function request({type, url, data,success,error}){
    // let userSID=sessionStorage.getItem('userSID');
    if(type!="file"&&userSID){
      data=Object.assign({},data,{userSID:userSID });
    }
    if(type === 'get'){
        return get(url,data,success,error);
    }else if(type === 'post'){
        return post(url,data,success,error);
    }else if(type === 'file'){
        return upload(url,data,success,error);
    }
}



