<template>
 <div>
      <!--  -->
      <el-drawer
        :visible.sync="visible"
        direction="rtl"
        size="50%"
        :show-close="false"
        :wrapperClosable='false'
        :before-close="dialogClose">
          <div slot="title" class="title">{{title}}<div class="icon" @click="dialogClose"><i class="el-icon-close"></i></div></div>
          <div class="drawer-box">
           <el-form label-position="right" status-icon ref="postData" label-width="80px" :rules="postRules" :model="postData" class="demo-ruleForm">
             <el-form-item label="" prop="name">
              <el-input maxlength="6" v-model="postData.name" placeholder=""></el-input>
             </el-form-item>
             <el-form-item label="" prop="user_name">
              <el-input maxlength="10" v-model="postData.user_name"  placeholder="" ></el-input>
             </el-form-item>
             <el-form-item label="" prop="password" v-if="type!='edit'">
                 <el-input type="password" maxlength="18" v-model="postData.password" placeholder="(:123456)"  autocomplete="off"></el-input>
             </el-form-item>
             <el-form-item label="" v-else prop="passwords">
                 <el-input type="password" maxlength="18" v-model="postData.passwords" placeholder="()"  autocomplete="off"></el-input>
             </el-form-item>

             <el-form-item label="" prop="checkPass" v-if="postData.passwords&&postData.passwords.length>=6">
                 <el-input type="password" maxlength="18" v-model="postData.checkPass" placeholder=""  autocomplete="off"></el-input>
             </el-form-item>

             <el-form-item label=""  prop="tel" >
              <el-input maxlength="11"  v-model="postData.tel" placeholder="11"></el-input>
             </el-form-item>
             <el-form-item label="" prop="role_id">
                 <el-select v-model="postData.role_id" placeholder="">
                   <el-option v-for="item in role" :key="item.id" :label="item.role_name" :value="item.id"></el-option>
                 </el-select>
             </el-form-item>
             <el-form-item label="" prop="headimg">
                <Upload  :croppParame="croppParame" :isCrop="true" :imgList="imageUrl" :parame="uploadParame" @onSuccess="getImage"></Upload>
             </el-form-item>
           </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button size="small" @click="dialogClose"> </el-button>
              <el-button size="small" type="primary" @click="submitForm('postData')"> </el-button>
            </div>
          </div>
      </el-drawer>
 </div>
</template>

<script>
  import Upload from '@/components/upload';

  export default {
    components:{Upload},
    data(){
      return{
        //
        uploadParame:{
          data:{path:"admin"},//
          limit:1,//
          action:String, //
          action:this.$api.upload,//
        },
        imageUrl:[], //
        title:"",//
        visible:false,//
        postData:{
          name:"",
          user_name:"",
          tel:"",
          role_id:"",
          headimg:"",
          password:"",
          passwords:"",
          checkPass:""
        },
        type:"", //
        postRules:this.verification(), //
        // role:[], //
        handleNname:{
          edit:"editAdmin",
          add:"addAdmin",
          del:"delAdmin"
        },
        croppParame:{   //
            croppersW:"400px",//
            croppersH:"300px",//
            autoCropWidth:"200", //
            autoCropHeight:"200", //
            top:"30vh",           //
            topCrop:"25px"       //
        }
      }
    },
    props:{
      role:Array|Object
    },
    methods:{
      //
      verification(){
        //
        var validatePass2 = (rule, value, callback) => {
          if (this.postData.passwords!=""&&value !== this.postData.passwords) {
            callback(new Error('!'));
          } else {
            callback();
          }
        };
        var v=[
           {field:"name",type:{required:"",chinese:""},textCount:[2,6]},
           {field:"user_name",type:{required:"",strAZaz:""},textCount:[4,10]},
           {field:"password",type:{required:""},textCount:[6,18]},
           {field:"passwords",textCount:[6,18]},
           {field:"tel",type:{required:"",tel:""},textCount:[11,11],lengthText:"11"},
           {field:"role_id",type:{required:""}},
           {field:"checkPass",validator:validatePass2} //
        ];
        v=this.$verification(v);
        return v;
      },
      //
      getImage({set,imgUrl,index}){
        if(set.type==="add"){
          //
          this.imageUrl.push(imgUrl);
        }else if(set.type==="edit"){
          //
          this.$set(this.imageUrl,set.index,imgUrl);
        }else if(set.type==="del"){
          //
          this.imageUrl=this.imageUrl.filter((item,i)=>{
            if(set.index!=i){
              return item;
            }
          });
        }
      },
      //
      dialogClose(t){
        this.postData={
          name:"",
          user_name:"",
          tel:"",
          role_id:"",
          headimg:"",
          password:"",
          passwords:"",
          checkPass:""
        }
        this.imageUrl=[];
        this.visible=false;
        this.$emit('close',t);
      },
      operation(type,d){
        this.type=type;
        if(type==="edit"){
          let {id,name,user_name, tel,role_id,headimg}=d.row;
          this.title="";
          this.postData={
            id:id,
            name:name,
            user_name:user_name,
            tel:tel,
            role_id:role_id,
            headimg:headimg,
            password:"",
            passwords:"",
            checkPass:""
          };
          this.imageUrl=headimg.split(",");
          this.visible=true;
        }else if(type==="del"){
          let {id,name,user_name}=d.row;
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.submitData({id:id,type:'del'});
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }else if(type==="add"){
          this.title="";
          this.visible=true;
        }

      },
      //
      submitForm(formName){
        this.postData.headimg=this.imageUrl.join(",");
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let data=this.postData;
            data.type=this.type;
            if(this.type==='edit'){
              data.password=this.postData.passwords;
            }
            this.submitData(data);
          } else {
            return false;
          }
        });
      },
      submitData(data){
          const loading = this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          this.$request({
          	type:"post",
          	url:this.$api.addAdmin,
            data:data,
          	success:(res)=>{
              loading.close();
              if(res.code==200){
                this.visible=false;
                this.dialogClose('load');
                this.$message({
                  showClose: true,
                  message: res.msg,
                  type: 'success'
                });
              }else{
               this.$message({
                 showClose: true,
                 message: res.msg,
                 type: 'error'
               });
              }
          	},error:(err)=>{
          		this.$message.error(err);
          	}
          });
      }

    }
  }

</script>

<style scoped>
@import url("../../../assets/css/main.css");
.title{
  padding:0 20px;
  height:60px;
  line-height: 60px;
  font-size: 16px;
  border-bottom: 1px solid #f7f7f7;
}
.title .icon{
 float: right;
 cursor: pointer;
}
.drawer-box{
  padding-top: 15px;
  padding-right: 15px;
  padding-left: 15px;
}
.dialog-footer{
  text-align: left;
}
</style>



